
"use client";

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { Play, Pause, RefreshCw, Minus, Plus, Loader2 } from "lucide-react";
import { Slider } from "@/components/ui/slider";
import { cn } from "@/lib/utils";
import { useToast } from "@/hooks/use-toast";
import { generatePracticeTopics, type GeneratePracticeTopicsInput } from "@/ai/flows/generate-practice-topics-flow";

type PracticeMode = "extemp" | "impromptu";
type Topic = { id: number; text: string };

export default function ExtempPracticePage() {
    const { toast } = useToast();
    const [mode, setMode] = React.useState<PracticeMode>("extemp");
    const [isLoadingTopics, setIsLoadingTopics] = React.useState(false);
    const [topics, setTopics] = React.useState<Topic[]>([]);
    const [selectedTopic, setSelectedTopic] = React.useState<Topic | null>(null);
    const [notes, setNotes] = React.useState("");

    const [prepTime, setPrepTime] = React.useState(30 * 60);
    const [speechTime, setSpeechTime] = React.useState(7.5 * 60);

    const [prepTimerActive, setPrepTimerActive] = React.useState(false);
    const [speechTimerActive, setSpeechTimerActive] = React.useState(false);

    const prepTimeIntervalRef = React.useRef<NodeJS.Timeout | null>(null);
    const speechTimeIntervalRef = React.useRef<NodeJS.Timeout | null>(null);
    
    React.useEffect(() => {
        return () => {
            if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
            if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
        };
    }, []);

    React.useEffect(() => {
        if (prepTime <= 0) {
            if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
            setPrepTimerActive(false);
        }
    }, [prepTime]);

    React.useEffect(() => {
        if (speechTime <= 0) {
            if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
            setSpeechTimerActive(false);
        }
    }, [speechTime]);

    const handleModeChange = (newMode: string) => {
        setMode(newMode as PracticeMode);
        resetPractice();
        if (newMode === 'impromptu') {
            setPrepTime(2 * 60);
            setSpeechTime(5.5 * 60);
        } else {
            setPrepTime(30 * 60);
            setSpeechTime(7.5 * 60);
        }
    };

    const handleGenerateTopics = async () => {
        setIsLoadingTopics(true);
        resetPractice();
        try {
            const input: GeneratePracticeTopicsInput = { type: mode };
            const result = await generatePracticeTopics(input);
            setTopics(result.topics.map((text, id) => ({ id, text })));
        } catch (error) {
            console.error("Failed to generate topics:", error);
            toast({
                variant: "destructive",
                title: "Error",
                description: "Could not generate topics. Please try again.",
            });
        } finally {
            setIsLoadingTopics(false);
        }
    };
    
    const handleTopicSelect = (topic: Topic) => {
        if(selectedTopic) return;
        setSelectedTopic(topic);
        if (mode === 'extemp') {
            startPrepTimer();
        }
    };

    const resetPractice = () => {
        setTopics([]);
        setSelectedTopic(null);
        setNotes("");
        setPrepTimerActive(false);
        setSpeechTimerActive(false);
        if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
        if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
        if (mode === 'extemp') {
            setPrepTime(30 * 60);
            setSpeechTime(7.5 * 60);
        } else {
            setPrepTime(2 * 60);
            setSpeechTime(5.5 * 60);
        }
    };

    // Timer controls
    const startPrepTimer = () => {
        if (prepTime <= 0) return;
        setPrepTimerActive(true);
        prepTimeIntervalRef.current = setInterval(() => {
            setPrepTime(prev => prev - 1);
        }, 1000);
    };

    const pausePrepTimer = () => {
        setPrepTimerActive(false);
        if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
    };

    const startSpeechTimer = () => {
        if (speechTime <= 0) return;
        setSpeechTimerActive(true);
        speechTimeIntervalRef.current = setInterval(() => {
            setSpeechTime(prev => prev - 1);
        }, 1000);
    };

    const pauseSpeechTimer = () => {
        setSpeechTimerActive(false);
        if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
    };
    
    const handleImpromptuSlider = (value: number[]) => {
        const newPrepTime = value[0] * 60;
        setPrepTime(newPrepTime);
        setSpeechTime((7.5 * 60) - newPrepTime);
    };

    const formatTime = (seconds: number) => {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${String(mins).padStart(2, '0')}:${String(secs).padStart(2, '0')}`;
    };

    const renderTopic = (topic: Topic) => (
        <Card 
            key={topic.id}
            className={cn(
                "cursor-pointer transition-all hover:bg-primary/10",
                selectedTopic && selectedTopic.id !== topic.id ? "hidden" : "",
                selectedTopic && selectedTopic.id === topic.id ? "bg-primary text-primary-foreground border-primary shadow-lg" : ""
            )}
            onClick={() => handleTopicSelect(topic)}
        >
            <CardContent className="p-4 text-center">
                <p className="font-medium">{topic.text}</p>
            </CardContent>
        </Card>
    );

    return (
        <div className="flex flex-col gap-6">
            <div>
                <h1 className="text-3xl font-bold font-headline">Extemp / Impromptu Practice</h1>
                <p className="text-muted-foreground">Hone your speaking skills with timed practice sessions.</p>
            </div>

            <Tabs value={mode} onValueChange={handleModeChange} className="w-full">
                <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="extemp">Extemporaneous</TabsTrigger>
                    <TabsTrigger value="impromptu">Impromptu</TabsTrigger>
                </TabsList>
                <TabsContent value="extemp" />
                <TabsContent value="impromptu" />
            </Tabs>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                <div className="lg:col-span-2 space-y-6">
                    <Card>
                        <CardHeader>
                            <CardTitle>Topic Generator</CardTitle>
                            <CardDescription>
                                {mode === 'extemp' 
                                    ? 'Generate three current event questions. Choose one to start your 30-minute prep time.'
                                    : 'Generate three prompts. Choose one and use your adjustable prep time before speaking.'}
                            </CardDescription>
                        </CardHeader>
                        <CardContent className="space-y-4">
                            <div className="flex gap-2">
                                <Button onClick={handleGenerateTopics} className="w-full" disabled={isLoadingTopics}>
                                    {isLoadingTopics ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                                    Generate Topics
                                </Button>
                                <Button variant="outline" size="icon" onClick={resetPractice}><RefreshCw className="h-4 w-4" /></Button>
                            </div>
                            
                            {topics.length > 0 && (
                                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                    {topics.map(renderTopic)}
                                </div>
                            )}
                        </CardContent>
                    </Card>
                    <Card>
                        <CardHeader>
                            <CardTitle>Notes</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <Textarea 
                                placeholder="Start typing your notes here..."
                                className="min-h-[300px] text-base"
                                value={notes}
                                onChange={(e) => setNotes(e.target.value)}
                            />
                        </CardContent>
                    </Card>
                </div>
                <div className="lg:col-span-1 space-y-6">
                    <Card>
                        <CardHeader>
                            <CardTitle>Prep Timer</CardTitle>
                        </CardHeader>
                        <CardContent className="flex flex-col items-center gap-4">
                             <div className="text-6xl font-bold font-mono tabular-nums">{formatTime(prepTime)}</div>
                            <div className="flex gap-2">
                                <Button size="icon" onClick={prepTimerActive ? pausePrepTimer : startPrepTimer} disabled={prepTime <= 0}>
                                    {prepTimerActive ? <Pause /> : <Play />}
                                </Button>
                            </div>
                            {mode === 'impromptu' && (
                                <div className="w-full space-y-2 pt-4">
                                    <label className="text-sm font-medium">Adjust Prep Time</label>
                                    <Slider
                                        min={0}
                                        max={7.5}
                                        step={0.5}
                                        value={[prepTime/60]}
                                        onValueChange={handleImpromptuSlider}
                                        disabled={prepTimerActive || speechTimerActive}
                                    />
                                </div>
                            )}
                        </CardContent>
                    </Card>
                    <Card>
                        <CardHeader>
                            <CardTitle>Speech Timer</CardTitle>
                        </CardHeader>
                        <CardContent className="flex flex-col items-center gap-4">
                            <div className="text-6xl font-bold font-mono tabular-nums">{formatTime(speechTime)}</div>
                             <div className="flex gap-2">
                                <Button size="icon" onClick={speechTimerActive ? pauseSpeechTimer : startSpeechTimer} disabled={speechTime <= 0}>
                                    {speechTimerActive ? <Pause /> : <Play />}
                                </Button>
                            </div>
                        </CardContent>
                    </Card>
                </div>
            </div>

        </div>
    );
}
